.footer-content {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-evenly;
    padding: 2%;
}

.footer {
    background: linear-gradient(90deg, rgba(9,10,14,1) 0%, rgba(106,106,106,1) 51%, rgba(9,10,14,1) 100%);
}

.icon-link > img {
    width: 50px;
    padding: 5%;
}

.footer-icon {
    width: 90px;
    padding: 10%;
}

.icons-wrapper {
    display: flex;
    flex-direction: column;
}